.avatar {
    width: 150px;
    height: 150px;
    margin: auto;
    margin-bottom: 1em;
    border-radius: 50%;

    align-self: center;

    img {
        width: 100%;
        border-radius: 50%;
        box-shadow: 0 0 13px #333;
    }
}

@media (max-width: 500px) {
    .avatar {
        width: 100px;
        height: 100px;
    }
}

@media (max-width: 800px) {
    .avatar {
        width: 120px;
        height: 120px;
        margin-bottom: 1em;
    }
}
