.contacts {
    display: flex;
    margin: 2.3em 0 1em;

    justify-content: center;

    &__item {
        margin: 0 1em;
        text-decoration: none;
        color: #fff;

        &:hover {
            text-decoration: underline;
        }
    }
}

@media (max-width: 700px) {
    .contacts {
        flex-direction: column;

        &__item {
            margin: 0.5em 0;
        }
    }
}
