// @import "normalize";
@import "typography";
@import "header";
@import "avatar";
@import "contacts";
@import "work";

html {
    box-sizing: border-box;
    background-color: #f3f3f3;
}

body {
    margin: 0;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

.container {
    width: 90%;
    margin-right: auto;
    margin-left: auto;

    @media only screen and (min-width: 33.75em) {
        width: 80%;
    }

    @media only screen and (min-width: 60em) {
        width: 75%;
        max-width: 60rem;
    }
}
