html {
    color: #333;
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;

    @media (max-width: 500px) {
        font-size: 0.75rem;
    }

    @media (max-width: 900px) {
        font-size: 1rem;
    }

    @media (min-width: 1400px) {
        font-size: 1.2rem;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
    margin-top: 0;
    margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-bottom: 0.5rem;
    color: darken(#333, 0.5%);
    font-family: "Roboto Slab", serif;
    font-weight: 700;
    line-height: 1.1;
}

a[href^="http"],
a[href^="tel"],
a[href^="mailto"],
a[href*="t.me"],
a[href*="github"] {
    position: relative;
    padding-left: 1em;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);

    &:not(.contacts__item) {
        color: #666;

        &:hover {
            color: #ff512f;
        }
    }

    &:before {
        position: absolute;
        left: 0;
        display: block;
        content: "";
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
    }

    &:not([href^="http"]):not([href^="mailto"]):not([href*="github"]):before {
        top: 0.4em;
    }
}

a[href^="http"]:before {
    top: 0.55em;
    width: 0.65em;
    height: 0.65em;
    background-image: url(../img/icon-link-external.svg);
}

a[href^="tel"]:before,
a[href*="github"]:before,
a[href*="t.me"]:before,
a[href^="mailto"]:before {
    top: 0.38em;
    width: 0.8em;
    height: 0.8em;
}

a[href^="mailto"]:before {
    top: 0.47em;
    background-image: url(../img/e-mail-envelope.svg);
}

a[href^="tel"]:before {
    background-image: url(../img/phone-receiver.svg);
}

a[href*="github"]:before {
    background-image: url(../img/github.svg);
}

a[href*="t.me"]:before {
    background-image: url(../img/telegram-logo.svg);
}
